<template>
  <div class="search-box-wrapper">
    <i class="icon-search"></i>
    <!-- v-model双向绑定指令 -->
    <input ref="query" class="box" :placeholder="placeholder" v-model="query"/>
    <i class="icon-delete" v-show="query" @click="clear"></i>
  </div>
</template>

<script type="text/ecmascript-6">
  import {debounce} from 'common/js/util'

  export default {
    props: {
        placeholder: {
            type: String,
            default: '搜索'
        }
    },
    data () {
      return {
          query: ''
      }
    },
    created () {
        // 暂时在created中监听query属性
        this.$watch('query', debounce((newQuery) => {
            this.$emit('query', newQuery)
        }, 200))
    },
    methods: {
        clear() {
            this.query = ''
        },
        setQuery(query) {
            this.query = query
        },
        blur() {
            this.$refs.query.blur()
        }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .search-box-wrapper
    display flex
    width 100%
    height 32px
    align-items center
    background #fff
    position relative
    .icon-search
      background url("./search.png") no-repeat
      background-size contain
      width 11px
      height 11px
      position absolute
      left 12px
    .icon-delete
      background url("./search.png") no-repeat
      background-size contain
      width 11px
      height 11px
      position absolute
      right 12px
      &:before
        position absolute
        left -10px
        top -10px
        right -10px
        bottom -10px
        content ''
    .box
      display flex
      flex 1
      height 100%
      background #F0F0F0
      color #3d3d3d
      border-radius 16px
      font-size $font-size-small-x
      outline none
      box-sizing border-box
      padding-left 36px
      padding-right 36px
      &::placeholder
        color #999

</style>



